import get from 'lodash/get';
import React, { PropTypes } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import * as actionCreators from 'actions';
import { Button, Divider, Form, Icon, Input, Segment } from 'semantic-ui-react';
import { AuthWrapper, NProgress } from 'components';

const mapStateToProps = (state) => ({
  ...state.auth
});
const mapDispatchToProps = (dispatch) => ({
  dispatch,
  actions: {
    alert: bindActionCreators(actionCreators.alert, dispatch),
    auth: bindActionCreators(actionCreators.auth, dispatch)
  }
});

@AuthWrapper
@connect(mapStateToProps, mapDispatchToProps)
export default class Submit extends React.Component {
  static propTypes = {
    error: PropTypes.any,
    dispatch: PropTypes.func.isRequired,
    actions: PropTypes.object.isRequired,
    location: PropTypes.object.isRequired
  };
  state = {
    title: '',
    isLoading: false
  };
  constructor(props) {

    super(props);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }
  componentDidMount() {

    NProgress.done();
  }
  componentDidUpdate() {

    NProgress.done();
  }
  hasValidationError(validationKey) {

    return (get(this.props.error, 'validation.keys') && this.props.error.validation.keys.includes(validationKey)) ? true : false;
  }
  handleSubmit(event) {

    event.preventDefault();
    this.setState({ isLoading: true }, () => {

      const { title } = this.state;
      this.props.actions.listings.create({ title })
        .then(() => this.props.dispatch(push('/')))
        .catch((error) => {

          this.setState({ isLoading: false });
          this.props.actions.alert.error(error.message);
        });
    });
  }
  handleChange(event) {

    this.setState({ [event.target.name]: event.target.value });
  }
  render() {

    return (
      <Segment as={Form} attached='bottom'>
        <Form.Field error={this.hasValidationError('title')}>
          <label>Title</label>
          <Input
            name="title"
            onChange={this.handleChange}
            placeholder='Title'
            type="text"
            value={this.state.title}
          />
        </Form.Field>
        <Divider horizontal>
          <Icon circular
            name='photo'
            color='blue'
          />
        </Divider>
        <Segment>
          <Button fluid
            content='Select Photos'
          />
        </Segment>
        <Divider horizontal>
          <Icon circular
            name='cart'
            color='blue'
          />
        </Divider>
        <Form.Group>
          <Form.Field error={this.hasValidationError('cardNumber')} width='6'>
            <label>Card Number</label>
            <Input
              icon='credit card alternative'
              iconPosition='left'
              name="cardNumber"
              onChange={this.handleChange}
              placeholder='Card Number'
              type="text"
              value={this.state.cardNumber}
            />
          </Form.Field>
          <Form.Field error={this.hasValidationError('cardExpirationMonth')} width='4'>
            <label>Expiration Month</label>
            <Input
              name="cardExpirationMonth"
              onChange={this.handleChange}
              placeholder='Expiration Month'
              type="text"
              value={this.state.cardExpirationMonth}
            />
          </Form.Field>
          <Form.Field error={this.hasValidationError('cardExpirationYear')} width='4'>
            <label>Expiration Year</label>
            <Input
              name="cardExpirationYear"
              onChange={this.handleChange}
              placeholder='Expiration Year'
              type="text"
              value={this.state.cardExpirationYear}
            />
          </Form.Field>
          <Form.Field error={this.hasValidationError('cardCvc')} width='2'>
            <label>CVC</label>
            <Input
              name="cardCVC"
              onChange={this.handleChange}
              placeholder='CVC'
              type="text"
              value={this.state.cardCVC}
            />
          </Form.Field>
        </Form.Group>
        <Button fluid primary
          disabled={this.state.isLoading}
          loading={this.state.isLoading}
          onClick={this.handleSubmit}
          content='Create'
        />
      </Segment>
    );
  }

}



